.all{width: 100%;background: #f7f7f7;}
.router-link-active {
    text-decoration: none;
}
/*navheader页面*/
.header{z-index: 1;-webkit-box-sizing: border-box;box-sizing: border-box;height: 0.9rem;background: #fff;width: 100%;text-align: center;position: fixed;top: 0;padding:0 0.25rem ;font-size: 0.3rem;}
.header-select{border-radius:0.1rem ;background: #f2f2f2;width: 100%;height: 0.7rem;margin: 0.1rem 0;}
.header-address{float: left;width: 1.2rem;height: 0.7rem;line-height: 0.7rem;}
.header input{background: none;float: left;color: #8d8d8d;width: 5.7rem;height: 0.7rem;outline: none;border: 0;padding:0.05rem 0.2rem 0 0.8rem;-webkit-box-sizing: border-box;box-sizing: border-box;font-size: 0.24rem;}
.header img{position: absolute;left: 1.7rem;top: 0.25rem;width: 0.4rem;padding-right:0.2rem ;}
.header span{border-right:1px solid #9d9d9d ;float: left;height: 0.4rem;margin-top: 0.15rem;}
/*index*/
.index{width: 100%;margin-bottom: 0.9rem;}
.index-top{width: 100%;margin-top: 1.2rem;background: white;margin-bottom: 0.25rem;padding-bottom: 0.3rem;border-bottom: 1px solid #f3f3f3;}
.index-top-chunk{width: 20%;float: left;text-align: center;font-size: 0.22rem;}
.index-top-ico{width: 1rem;height: 1rem;margin: 0 auto;border-radius: 50%;margin-bottom: 0.14rem;}
.index-top-ico img{width: 0.8rem;margin-top: 0.11rem;}
.index-top-ico.ye{background: #f9c45a;}
.index-top-ico.or{background: #ea8638;}
.index-top-ico.bl{background: #75dcd8;}
.index-top-ico.pu{background: #9863ff;}
.index-top-ico.re{background: #fd5d81;}
.index-list{background: white;width: 100%;-webkit-transition: all 0.3s;transition: all 0.3s;position: relative;}
.index-list-header{height: 1rem;font-size: 0.3rem;color: #858585;border-bottom: 1px solid #f3f3f3;}
.index-list-header-frame.active span{opacity: 1;}
.index-list-header-frame span{opacity: 1;-webkit-transition: all 0.3s;transition: all 0.3s;}
.index-list-header-frame{float: left;width: 33.3%;text-align: center;line-height: 1rem;position: relative;color: #52505b;}
.index-list-frame{width: 100%;}
.index-list-frame-list{height: 1.8rem;border-bottom:1px solid #F3F3F3 ;font-family: "\9ED1\4F53";}
.frame-list-img{width: 2rem;height: 1.4rem;margin: 0.2rem;float: left;}
.frame-list-img img{-o-object-fit: cover;object-fit: cover;}
.frame-list-1{float: left;margin-top: 0.3rem;width: 5rem;}
.frame-list-2{float: left;margin-top: 0.1rem;width: 5rem;}
.frame-list-title{font-size: 0.26rem;float: left;width: 4.5rem;overflow: hidden;text-overflow: ellipsis;white-space: nowrap;}
.frame-list-day{margin-right: 0.1rem;font-size: 0.22rem;float: right;height: 0.3rem;border: 1px solid #1296DB;line-height: 0.32rem;color: #1296DB;text-align: center;padding: 0 0.1rem;}
.frame-list-time{font-size: 0.22rem;color: #adadad;float: left;line-height: 0.4rem;}
.frame-list-money{font-size: 0.26rem;color: #f19b5e;float: right;margin-right: 0.2rem;line-height: 0.4rem;}
.frame-list-baoming{margin-right: 0.1rem;font-size: 0.22rem;float: left;height: 0.3rem;border: 1px solid #42B983;line-height: 0.32rem;color: #42B983;text-align: center;padding: 0 0.1rem;}
/*navfooter页面*/
.footer{position: fixed;bottom: 0;width: 100%;border-top:1px solid #F2F2F2 ;background: white;z-index: 1;}
.footer-button{width: 25%;float: left;text-align: center;height: 0.9rem;font-size: 0.28rem;padding-top: 0.2em;}
.footer-button img{width: 0.4rem;margin: 0 auto 0.1rem auto;}
.footer-button.active{color: #1296db;}
.actimg{display: none;}
.noneimg{display: block;}
.active .actimg{display: block;}
.active .noneimg{display: none;}
.footer-button div{width: 100%;}
@charset "utf-8";
/* CSS Document */
/*Reset*/
*{-webkit-box-sizing:content-box;box-sizing:content-box;}
a:hover, a:focus{text-decoration:none;}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,p,blockquote,th,td{margin:0;padding:0;}
table{border-collapse:collapse;border-spacing:0;}
fieldset,img{border:0;}
img{ max-width: 100%; }
address,caption,cite,code,dfn,em,th,var{font-style:normal;font-weight:normal;}
ol,ul{list-style:none;}
caption,th{text-align:center;}
h1,h2,h3,h4,h5,h6{font-size:100%;font-weight:normal;}
q:before,q:after{content:'';}
abbr,acronym {border:0;}
.clearfix:after{visibility:hidden;display: block;font-size:0;content:" ";clear:both;height:0;}
* html .clearfix{ zoom: 1; }
/* IE6 */
*:first-child+html .clearfix { zoom: 1; }
/* IE7 */
.cli{ clear:both; font-size:0; height:0; overflow:hidden;display:block;}
.lclear{clear:left;font-size:0;height:0;overflow:hidden;}
.fl{float:left;}
.fr{float:right;}
