body{font-size:12px;font-family:"Helvetica Neue", Helvetica, Arial, "PingFang SC", "Hiragino Sans GB", "Heiti SC", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif, sans-serif;color:#000;line-height:1.2;text-align:left;}
a{color:#000;text-decoration:none;}

.index-popur{z-index:1;position:absolute;left:0;width:100%;top:1.2rem;height:100%;background:rgba(0,0,0,.5);overflow:hidden;-webkit-transition:all .3s;transition:all .3s
}
.index-list-block{background:#fff;padding:0 .2rem;-webkit-box-sizing:border-box;box-sizing:border-box;width:100%;color:#666;background:#fff
}
.index-list-block-line{height:.8rem;width:100%;font-size:.3rem;border-bottom:1px solid #ccc;line-height:.8rem
}
.index-list-block-line:last-child{border:0
}
.triangle{display:inline-block;position:absolute;top:.4rem;right:.05rem;cursor:pointer
}
.triangleHidden{border-left:.1rem solid transparent;border-right:.1rem solid transparent;border-bottom:.16rem solid #676F7F
}
.triangleShow{border-left:.1rem solid transparent;border-right:.1rem solid transparent;border-top:.16rem solid #676F7F
}














